import React, { Component } from 'react';

export default class TrashMasonryMedia extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
      // const image = await loadImage(imgUrl);
      // this.setState({
      //     height: (image.height * 100) / image.width,
      //     width: (image.width * 100) / image.height
      // })
    }
  }

  render() {
    const { height, width, alt } = this.props
    const { imgUrl } = this.state

    const imageStyle = {
      paddingBottom: 100 + '%', width: '100%'
    };

    return <div className="position-relative rounded-1 bg-light overflow-hidden" style={imageStyle}>
      <img className="img-fluid position-absolute h-100 w-100 object-center-cover" loading="lazy" src={imgUrl} height={height} width={width} alt={alt ? alt : 'suit image'} />
    </div>
  }
}
